const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001';

interface ApiError {
  message: string;
  status?: number;
}

export const api = {
  async fetchArticles(): Promise<Article[]> {
    return this.handleRequest('/articles');
  },

  async fetchArticle(id: string): Promise<Article> {
    return this.handleRequest(`/articles/${id}`);
  },

  async createArticle(data: ArticleFormData): Promise<Article> {
    return this.handleRequest('/articles', {
      method: 'POST',
      body: JSON.stringify(data)
    });
  },

  async updateArticle(id: string, data: ArticleFormData): Promise<Article> {
    return this.handleRequest(`/articles/${id}`, {
      method: 'PUT',
      body: JSON.stringify(data)
    });
  },

  async deleteArticle(id: string): Promise<void> {
    return this.handleRequest(`/articles/${id}`, {
      method: 'DELETE'
    });
  },

  private async handleRequest<T>(endpoint: string, options?: RequestInit): Promise<T> {
    try {
      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        headers: {
          'Content-Type': 'application/json',
        },
        ...options
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw {
          message: errorData.message || 'Erreur API',
          status: response.status
        } as ApiError;
      }

      return await response.json();
    } catch (error) {
      throw {
        message: (error as ApiError).message || 'Erreur réseau',
        status: (error as ApiError).status || 500
      };
    }
  }
};