import { api } from '@/lib/api';
import Link from 'next/link';

export default async function ArticlesPage() {
  const { data: articles } = await api.getArticles();

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Liste des articles</h1>
      <Link href="/articles/create" className="bg-blue-500 text-white px-4 py-2 rounded mb-4 inline-block">
        Créer un article
      </Link>

      <div className="space-y-4">
        {articles.map(article => (
          <div key={article.id} className="border p-4 rounded">
            <h2 className="text-xl font-semibold">{article.title}</h2>
            <p className="text-gray-600 mt-2">{article.content}</p>
            <div className="mt-4 flex gap-2 text-sm">
              <Link 
                href={`/articles/edit/${article.id}`}
                className="text-blue-500 hover:text-blue-700"
              >
                Modifier
              </Link>
              <form action={async () => {
                'use server';
                await api.deleteArticle(article.id);
              }}>
                <button type="submit" className="text-red-500 hover:text-red-700">
                  Supprimer
                </button>
              </form>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}








